interface Material {
  id: string;
  name: string;
  type: "material";
  studies: string[];
}

interface Study {
  id: string;
  name: string;
  type: "study";
  protocols: string[];
}

interface Protocol {
  id: string;
  name: string;
  type: "protocol";
  tests: string[];
}

interface Test {
  id: string;
  name: string;
  type: "test";
  duration: number;
}

interface resources {
  id: string;
  name: string;
  status: string;
}

export interface Data {
  materials: Material[];
  studies: Study[];
  protocols: Protocol[];
  tests: Test[];
  resources: resources[];
}

interface DirStructure<T> {
  id: string;
  name: string;
  data: T;
  type: "dir" | "file";
  children: DirStructure<T>[];
}

interface MaterialCardProps {
  // material: Material;
  onSelectMaterial: (material: Material) => void;
}

const MaterialCard: React.FC<MaterialCardProps> = ({ onSelectMaterial }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedMaterialId, setSelectedMaterialId] = useState<number | null>(
    null
  );

  const filteredMaterials = dummyData.materials.filter((m) =>
    m.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <Card
      style={{ backgroundColor: "rgba(255, 0, 0, 0.6)", padding: "10px" }}
      maxW="250px"
      w="100%"
      p="10px"
    >
      <CardHeader display="flex" justifyContent="space-between">
        <Heading size="md">Materials</Heading>
        <Input
          htmlSize={7}
          width="auto"
          ml="30px"
          size="xs"
          placeholder="search"
          variant="filled"
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </CardHeader>

      <CardBody>
        <Stack divider={<StackDivider />} spacing="4">
          {filteredMaterials.map((material: any) => (
            <Box
              key={material.id}
              p="4"
              // borderWidth="1px"
              // borderRadius="md"
              _hover={{
                bg: "gray.100", // Background color on hover>
                // boxShadow: "0 0 10px rgba(255, 255, 255, 0.8)",
              }}
              onClick={() => {
                setSelectedMaterialId(material.id);
                onSelectMaterial(material);
              }}
              bg={selectedMaterialId === material.id ? "gray.300" : "white"} // Background color if selected
            >
              <Heading size="sm">{material.name}</Heading>
            </Box>
          ))}
        </Stack>
      </CardBody>
    </Card>
  );
};

const StudyCard = ({ selectedMaterial, onSelectStudies }: any) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedMaterialId, setSelectedMaterialId] = useState<number | null>(
    null
  );
  console.log("studies", selectedMaterial.studies);

  const filteredStudies = dummyData.studies.filter(
    (study) =>
      selectedMaterial.studies.includes(study.id) &&
      study.name.toLowerCase().includes(searchTerm.toLowerCase())
  );
  return (
    <Card
      style={{ backgroundColor: "rgba(255, 0, 0, 0.6)" }}
      maxW="250px"
      w="100%"
    >
      <CardHeader display="flex" justifyContent="space-between">
        <GoArrowLeft size={30} style={{ cursor: "pointer" }} />
        <Heading size="md">Studies</Heading>
        <Input
          htmlSize={7}
          width="auto"
          ml="30px"
          size="xs"
          placeholder="search"
          variant="filled"
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </CardHeader>

      <CardBody>
        <Stack divider={<StackDivider />} spacing="4">
          {filteredStudies.map((material: any) => (
            <Box
              key={material.id}
              p="4"
              // borderWidth="1px"
              // borderRadius="md"
              _hover={{
                bg: "gray.100", // Background color on hover>
              }}
              onClick={() => {
                onSelectStudies(material);
                setSelectedMaterialId(material.id);
              }}
              bg={selectedMaterialId === material.id ? "gray.300" : "white"} // Background color if selected
            >
              <Heading size="sm">{material.name}</Heading>
            </Box>
          ))}
        </Stack>
      </CardBody>
    </Card>
  );
};

